import * as React from "react";
import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import * as styles from "./trip-container.module.css";

type TripContainerType = {
  destinationPrice?: string;
  destinationPriceRange?: string;

  /** Style props */
  propBackgroundImage?: Property.BackgroundImage;
  propWidth?: Property.Width;
  propDisplay?: Property.Display;
  propWidth1?: Property.Width;
};

const TripContainer: FunctionComponent<TripContainerType> = ({
  destinationPrice,
  destinationPriceRange,
  propBackgroundImage,
  propWidth,
  propDisplay,
  propWidth1,
}) => {
  const parisCardStyle: CSS.Properties = useMemo(() => {
    return {
      backgroundImage: propBackgroundImage,
    };
  }, [propBackgroundImage]);

  const detailsStyle: CSS.Properties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const divStyle: CSS.Properties = useMemo(() => {
    return {
      display: propDisplay,
      width: propWidth1,
    };
  }, [propDisplay, propWidth1]);

  return (
    <button className={styles.col1}>
      <div className={styles.pariscard} style={parisCardStyle}>
        <div className={styles.destinationDetails}>
          <b className={styles.paris}>{destinationPrice}</b>
          <div className={styles.details} style={detailsStyle}>
            <div className={styles.from}>from</div>
            <div className={styles.div} style={divStyle}>
              {destinationPriceRange}
            </div>
          </div>
        </div>
      </div>
    </button>
  );
};

export default TripContainer;
