import * as React from "react";
import { FunctionComponent, useState, useMemo, useCallback } from "react";
import MatterhornPopup from "./matterhorn-popup";
import PortalPopup from "./portal-popup";
import CSS, { Property } from "csstype";
import * as styles from "./hotel-container.module.css";

type HotelContainerType = {
  hotelName?: string;
  accommodationType?: string;
  hotelImageUrl?: string;
  pricePerNight?: string;
  hotelDimensions?: string;
  hotelRating?: string;
  numberOfReviews?: string;

  /** Style props */
  propFlex?: Property.Flex;
  propGap?: Property.Gap;
};

const HotelContainer: FunctionComponent<HotelContainerType> = ({
  hotelName,
  accommodationType,
  hotelImageUrl,
  pricePerNight,
  hotelDimensions,
  hotelRating,
  numberOfReviews,
  propFlex,
  propGap,
}) => {
  const [isMatterhornPopupOpen, setMatterhornPopupOpen] = useState(false);
  const stayDetailsStyle: CSS.Properties = useMemo(() => {
    return {
      flex: propFlex,
      gap: propGap,
    };
  }, [propFlex, propGap]);

  const openMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(true);
  }, []);

  const closeMatterhornPopup = useCallback(() => {
    setMatterhornPopupOpen(false);
  }, []);

  return (
    <>
      <div className={styles.hotelCard1}>
        <div className={styles.hotelCard}>
          <img
            className={styles.matterhornSuitesImage}
            alt=""
            src={hotelName}
          />
          <div className={styles.stayDetails} style={stayDetailsStyle}>
            <div className={styles.stayDetailsRows}>
              <div className={styles.entireBungalow}>{accommodationType}</div>
              <b className={styles.matterhornSuites}>{hotelImageUrl}</b>
              <div className={styles.night}>{pricePerNight}</div>
            </div>
            <video
              className={styles.video}
              controls
              onClick={openMatterhornPopup}
            >
              <source />
            </video>
          </div>
          <div className={styles.rating}>
            <div className={styles.vectorParent}>
              <img className={styles.vectorIcon} alt="" src={hotelDimensions} />
              <div className={styles.div}>{hotelRating}</div>
            </div>
            <div className={styles.reviews}>{numberOfReviews}</div>
          </div>
          <button className={styles.moreDetailsButton}>
            <div className={styles.button}>More details</div>
          </button>
        </div>
      </div>
      {isMatterhornPopupOpen && (
        <PortalPopup
          overlayColor="rgba(113, 113, 113, 0.3)"
          placement="Centered"
          onOutsideClick={closeMatterhornPopup}
        >
          <MatterhornPopup onClose={closeMatterhornPopup} />
        </PortalPopup>
      )}
    </>
  );
};

export default HotelContainer;
